.comment-container {
    margin: 10px 0 10px 0;
    padding: 10px;
    background-color: #fff;
    border-left: 4px solid #6F1EB2;
    border-radius: 6px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 100%;
}

.comment-container h3 {
    font-weight: 500;
    font-size: 1.1rem;
    padding-left: 5px;
}

.comment-container .button {
    background-color: #F1F3FD;
    color: #111;
    border: 1px solid #6F1EB2;
    font-weight: 600;
    padding: 5px 10px;
    margin: 0 5px;
    border-radius: 4px;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 0.6rem;
}

.comment-container .input {
    padding: 5px;
    border: 1px solid #6F1EB2;
    border-radius: 4px;
    margin: 5px;
}

.comment-container input:focus {
    outline: none;
}
